.hero {
  position: relative;
  padding: 100px 0;
  height: 528px;
  background-color: var(--color-eastbay);

&__container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
  z-index: 5;
}


&__bg-text {
  position: absolute;
  left: 50%;
  top: 50%;
  color: rgba(255, 255, 255, 0.12);
font-size: 200px;
font-weight: 700;
transform: translate(-50%, -50%);

@include tablet {
  display: none;
}
}

&__title {
  margin: 0;
  margin-bottom: var(--offset-second);
  width: 50%;
  font: 700 36px / 140% var(--font-family);
  color: var(--color-white);

  @include mobile {
    width: 100%;
    font: 700 30px / 140% var(--font-family);
  }
}

&__descr {
  margin: 0;
  margin-bottom: var(--offset-main);
  max-width: calc(50% - 100px);
  font: var(--middle-book);
  color: var(--color-white);

  @include mobile {
   max-width: 100%;
  }
}

@include tablet {
height: auto;
}


}
