html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

// .is-hidden {
//   display: none !important; /* stylelint-disable-line declaration-no-important */
// }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0)
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.btn {
  padding: 22px 47px;
  border-radius: 4px;
  font: var(--middle-book);
  transition: color .3s ease-in-out, bacground-color .3s ease-in-out;

  &--primary {
    color: var(--color-white);
    background-color: var(--color-accent);

    @include desktop {
      &:hover {
        background-color: var(--color-accent-hover);
      }
    }

    &:active {
      background-color: #8c2828;
    }
  }

  &--secondary {
    color: var(--color-white);
    background-color: var(--color-main);

    @include desktop {
      &:hover {
        background-color: var(--color-main-hover);
      }
    }

    &:active {
      background-color: #8c2828;
    }
  }
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

