.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);

  @include mobile {
    grid-template-columns: repeat(4, 1fr);
  }
}

.title {
  margin: 0;
  margin-bottom: var(--offset-main);
  font: var(--middle-high-bold);
  color: var(--color-main);
}

.custom-checkbox {
  @include custom-checkbox;

  &__content {
    padding-left: 35px;
    font: var(--middle-book);
    color: var(--color-main);

    &::before {
      border-color: var(--color-cameo);
      border-radius: 4px;
      width: 24px;
      height: 24px;
    }

    &::after {
      width: 24px;
      height: 24px;
      background-image: url(../img/chek.svg);
      background-size: 15px 12px;
    }
  }
}

.tooltip {
  position: relative;
  margin-left: var(--offset-small);
}

.tooltip__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  color: var(--color-white);
  box-shadow: 2px 4px 10px 0px rgba(0, 13, 84, 0.20);
}

.tooltip__txt {
  position: absolute;
  left: -10px;
  bottom: calc(100% + 14px);
  z-index: 3;
  padding: 10px 20px;
  width: max-content;
  max-width: 426px;
  border-radius: 4px;
  color: var(--color-main);
  background: var(--color-white);
  font: var(--small-book);
  text-transform: initial;
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  transition: opacity .3s ease-in-out, transform .3s ease-in-out, visibility .3s ease-in-out;

  &::after {
    content: "";
    position: absolute;
    left: 10px;
    bottom: -8px;
    width: 0;
    height: 0;
    border-radius: 4px;
    border-style: solid;
    border-width: 10px 8px 0 8px;
    border-color: var(--color-white) transparent transparent transparent;
  }
}

.tooltip__btn:hover+.tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.tooltip__btn:focus+.tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.input {
  border: none;
padding: 0 var(--offset-second);
  width: 100%;
  height: 60px;
  border-radius: 4px;
  font: var(--middle-book);
  color: var(--color-main);
    background-color: var(--color-white);

  &::placeholder {
    color: var(--color-gray);
  }
}
