.graph-modal {
  @include tablet {
    &::before {
      display: none;
    }
  }
}


.prod-modal {
  padding: var(--offset-main);
  width: 1140px;

  &__top {
    margin-bottom: var(--offset-main);
    display: flex;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__bottom {
    margin-bottom: var(--offset-main);
  }

  &__video {
    width: 100%;
    height: 450px;

    iframe {
      display: block;
      border: none;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

@include tablet {
  margin: 0;
  width: 100%;
}



}

.modal-slider {
  margin-right: var(--offset-second);
  width: calc(50% - (var(--offset-second) / 2));



  &__container {
    margin-bottom: var(--offset-second);
    width: 100%;
    overflow: hidden;
    font-size: 0;

    img {
      width: 100%;
      max-height: 460px;
      object-fit: cover;
    }
  }

  @include small-tablet {
    margin-bottom: 30px;
    width: 100%;
  }
}

.modal-preview {
  display: grid;
  grid-template-columns: repeat(6, 70px);
  gap: var(--offset-second);

  &__item {
    border-radius: 4px;
    height: 75px;
    cursor: pointer;
    transition: opacity .3s ease-in-out;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include desktop {
      &:hover {
        opacity: 0.7;
      }
    }
  }
}

.modal-info {
  padding-right: var(--offset-main);
  width: calc(50% - (var(--offset-second) / 2));

  &__top {
    margin-bottom: var(--offset-main);
    display: flex;
    align-items: center;
    font: var(--middle-book);
    line-height: 16px;
    color: var(--color-gray);
  }

  &__vendor {
    margin-right: var(--offset-main);
  }

  &__quantity {
    span {
     color: var(--color-main);
    }
  }

&__title {
  margin: 0;
  margin-bottom: var(--offset-second);
  font: var(--prehigh-book);
  line-height: 140%;
  color: var(--color-main);
}

&__rate {
  margin-bottom: var(--offset-second);
}

&__sizes {
  margin-bottom: var(--offset-second);
}

&__subtitle {
  display: inline-block;
  margin-bottom: var(--offset-second);
  font: var(--middle-book);
  color: var(--color-main);
}

&__price {
  margin-bottom: 86px;
  display: flex;
  align-items: center;

  @include small-tablet {
    margin-bottom: 30px;
  }
}

&__current-price {
  margin-right: 32px;
  font: var( --middle-high-bold);
  line-height: 30px;
  color: var(--color-main);
}

&__old-price {
  font: var( --middle-book);
  line-height: 16px;
  color: var(--color-gray);
}

&__order {
  width: 100%;
  margin-bottom: var(--offset-main);
}

@include small-tablet {

  width: 100%;
}
}


.modal-sizes {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: var(--offset-second);

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 6px 0;
    width: 70px;
    border: 1px solid var(--color-gray);
    border-radius: 4px;
    font: var(--small-book);
    color: var(--color-main);
  }
}

.modal-note {
  &__item {
    padding-left: 25px;
    font: var(--small-book);
     color: var(--color-gray);
     background-image: url(../img/check-list.svg);
     background-position: left center;
     background-repeat: no-repeat;
     background-size: 15px 12px;


     &:not(:last-child) {
      margin-bottom: var(--offset-small);
     }
  }
}

.prod-bottom {
  display: flex;

  &__column {
    width: calc(50% - (var(--offset-second) / 2));

    &:first-child {
      margin-right: var(--offset-second);

      @include mobile {
        margin-bottom: 30px;
      }
    }

    @include mobile {
     width: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: var(--offset-second);
    font: var(--prehigh-book);
    color: var(--color-main);
  }

  &__descr {
    margin: 0;
    max-width: 430px;
    font: var(--middle-book);
    color: var(--color-main);
  }

  @include mobile {
    flex-direction: column;
  }
}

.prod-chars {
  &__item {
    &:not(:last-child) {
      margin-bottom: var(--offset-small);
    }
  }
}
