.product {
  overflow: hidden;
  border-radius: 4px;

  &__image {
    position: relative;
    width: 100%;
    max-height: 293px;

    img {
      display: block;
      margin-bottom: var(--offset-small);
      width: 100%;
      height: 293px;
      object-fit: cover;
    }
  }


  &__btns {
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    background: rgba(255, 255, 255, 0.60);
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

    @include tablet {
      opacity: 0.8;
      visibility: visible;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 80px;
    height: 80px;
    background-color: var(--color-main);
    transition: background-color .3s ease-in-out;

    &:first-child {
      margin-right: var(--offset-second);
    }

    svg {
      width: 30px;
      height: 30px;
      object-fit: cover;
    }

    @include desktop {
      &:hover {
        background-color: var(--color-main-hover);
      }
    }
  }


  &__title {
    margin: 0;
    margin-bottom: var(--offset-small);
    font: var(--middle-book);
    color: var(--color-main);
  }

  &__price {
    font: var(--prehigh-regular);
    color: var(--color-main);
  }

  @include desktop {
    &:hover {
      .product__btns {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.mini-product {
  display: flex;
  align-items: center;
  padding: var(--offset-small) var(--offset-second);

  &__image {
    flex-shrink: 0;
    margin-right: var(--offset-small);
    width: 100px;
    height: 100px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    display: flex;
    flex-grow: 1;
    align-items: center;
  }

  &__text {
    margin-right: auto;
    max-width: 268px;
  }

  &__title {
    margin: 0;
    margin-bottom: 14px;
    font: var(--small-book);
    line-height: 20px;
  }

  &__price {
    font: var(--prehigh-regular);
    font-weight: 700;
    color: #4d4d4d;
  }

  &__delete {
    flex-shrink: 0;
    width: 20px;
    height: 20px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
