.header {
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  background-color: var(--color-eastbay);

  &__container {
    position: relative;
    padding-top: var(--offset-second);
    padding-bottom: var(--offset-second);
    align-items: center;

    &::before {
      position: absolute;
      content: "";
      left: 50%;
      bottom: 0;
      height: 1px;
      width: calc(100% - var(--container-offset) * 2);
      background-color:  var(--color-border);
      transform: translateX(-50%);
    }

  }

  &__left {
    grid-column: 2 span;


  }

  &__right {
    grid-column: 10 span;
    display: flex;
    justify-content: flex-end;

    @include mobile {
      grid-column: 2 span;
      align-items: center;
    }


  }


  &__nav {
    @include tablet {
      position: absolute;
      top: 80px;
      left: 0;
      width: 100%;
transform: translateX(-100%);
transition: transform .3s ease-in-out;


    }

    &--active {
      transform: translateX(0);

    }
  }

  &__list {
    @include tablet{
      position: relative;
      height: 100vh;
      z-index: 1000;
      flex-direction: column;
      align-items: flex-start;
      padding: 20px;
      background-color: var(--color-eastbay);

      .nav__item {
        margin-right: 0;
        margin-bottom: 35px;
      }
    }
  }
}

.burger {
  @include burger;

  display: none;

  @include tablet {
    display: block;
  }
}
