.team {
  position: relative;
  background-color: var(--color-eastbay);
  padding: var(--offset-section) 0;

  &__figure {
    position: absolute;
    right: 0;
    top: 0;

    @include small-tablet {
      display: none;
    }
  }

  &__title {
    color: var(--color-white);
  }

  &__list {
    gap: var(--offset-main) var(--offset-second);
    position: relative;
    z-index: 10;
  }

  &__item {
    grid-column: 4 span;

    @include mobile {
      grid-column: 2 span;
    }
  }

  &__image {
    margin-bottom: var(--offset-second);
    overflow: hidden;
    border-radius: 4px;
  }

  &__subtitle {
    margin-bottom: var(--offset-second);
    display: block;
    font: var(--prehigh-book);
    color: var(--color-white);
  }

  &__post {
    display: block;
    font: var(--middle-book);
    color: var(--color-white);
  }
}
