.cart {
  position: relative;
  margin-left: var(--offset-main);

  &__btn {
    position: relative;
    width: 20px;
    height: 20px;
    outline: none;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--color-white);
      object-fit: cover;
      pointer-events: none;
      transition: fill .3s ease-in-out;
    }

    &:focus {
      svg {
        fill: var(--color-accent);
      }
    }


    @include desktop {
      &:hover {
        svg {
          fill: var(--color-accent);
        }
      }
    }

    &:active {
      svg {
        fill: var(--color-accent-hover);
      }
    }
  }

  &__count {
    position: absolute;
    right: -6px;
    bottom: -6px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    height: 16px;
    min-width: 16px;
    font: var(--small-book);
    color: var(--color-white);
    background-color: var(--color-accent);
    pointer-events: none;
  }
}



.mini-cart {
  position: absolute;
  top: calc(100% + 28px);
  right: 0;
  width: 480px;
  background-color: var(--color-white);
  box-shadow: 0px -4px 10px 0px rgba(0, 13, 84, 0.10);
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

  &--visible {
    opacity: 1;
    visibility: visible;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }

  &__top {
    max-height: 323px;

    .simplebar-track.simplebar-vertical {
      right: 5px;
      top: 20px;
      bottom: 20px;
      width: 4px;
      border-radius: 5px;
background: rgba(77, 77, 77, 0.20);
  }

    .simplebar-scrollbar::before {
      left: 0;
      right: auto;
      width: 4px;
opacity: 1;
border-radius: 5px;
background: rgba(77, 77, 77, 0.20);
    }
  }

  &__bottom {
    padding: var(--offset-second);
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0px -4px 10px 0px rgba(0, 13, 84, 0.10);
    background-color: var(--color-white);

    @include mobile {
      flex-wrap: wrap;
      }
  }

  &__itog {
    @include mobile {
      margin-bottom: 20px;
      }
  }

  &__txt {
    display: block;
 margin-bottom: var(--offset-small);
  }

  &__sum {
    font: var(--prehigh-regular);
    font-weight: 700;
    color: #4d4d4d;
  }

 &__btn {
  padding-top: 14px;
  padding-bottom: 14px;
 }

@include mobile {
width: calc(100vw - 30px);
}
}


