.sizes-table {
  border-collapse: collapse;
  width: 100%;

  caption{
    position: absolute;
    opacity: 0;
    pointer-events: none;
  }

  td {
    padding: 17px 5px;
    font: var(--middle-book);
    color: var(--color-main);
    text-align: center;
    border: 1px solid var( --color-cameo);
  }
}
