.catalog {
  padding-top: var(--offset-section);
  padding-bottom: var(--offset-section);

  &__reset {
    outline: none;
    transition: color .3s ease-in-out;

    &:focus {
      color: var(--color-accent);
    }

    @include desktop {
      &:hover {
        color: var(--color-accent);
      }
    }

    &:active {
      color: var(--color-accent-hover);
    }
  }

  &__left {
    grid-column: 3 span;
    border-radius: 4px;

    @include tablet {
      display: none;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: var(--offset-second);
    font: var(--prehigh-book);
  }

  &__prop {
    margin-bottom: var(--offset-second);
  }

  &__prop-checkbox {
    display: flex;
    justify-content: space-between;
  }

  &__caption {
    display: block;
    margin-bottom: var(--offset-small);
    font: var(--middle-book);
    color: var(--color-main);
  }

  &__grid {
    grid-column: 9 span;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
      grid-column: 12 span;
    }
  }

  &__apply {
    margin-bottom: var(--offset-second);
    width: 100%;
  }

  &__more {
    margin-top: var(--offset-section);
  }

}


.catalog-list {
  grid-template-columns: repeat(9, 1fr);

  @include mobile {
    grid-template-columns: repeat(4, 1fr);
  }

  &__item {
    grid-column: 3 span;

    @include mobile {
      grid-column: 4 span;
    }
  }
}

.catalog-filter {
  padding: var(--offset-second);
  border-radius: 4px;
  background-color: var(--color-bg);

  &__btns {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.catalog-price {
  &__wrapper {
    position: relative;
    display: flex;
    // flex-wrap: wrap;

    &::before {
      content: "";
      position: absolute;
      top: 8px;
      bottom: 8px;
      left: 50%;
      height: 34px;
      width: 1px;
      background-color: var(--color-gray);
      transform: translateX(-50%);

    }
  }

  &__input {
    width: 50%;
    height: 50px;
    font: var(--middle-book);
    color: var(--color-main);
    text-align: center;
    border: 1px solid var(--color-gray);
    background-color: transparent;

    &:first-child {
      border-right: none;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }

    &:last-child {
      border-left: none;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }
  }
}
