.insta {
  padding: var(--offset-section);

  &__container {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__left {
    margin-right: var(--offset-small);
    width: 380px;
    flex-shrink: 0;
    background-color: var(--color-eastbay);
    border-radius: 4px;

    @include tablet {
  width: 300px;
    }
  }

  &__title {
    margin-bottom: var(--offset-second);
    color: var(--color-white);
  }

  &__descr {
    margin: 0;
    margin-bottom: var(--offset-main);
    max-width: 260px;
    font: var(--middle-book);
    color: var(--color-white);
    text-align: center;
  }

  &__input {
    margin-bottom: var(--offset-small);
  }

  &__btn {
    width: 100%;
  }

  &__right {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(100% - (380px + var(--offset-small)));

    @include tablet {
      width: 100%;
        }
  }

  &__link-logo {
    margin-bottom: var(--offset-second);
  }


  &__list {
    display: flex;
    gap: 18px;
    max-width: 680px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 18px;
  }

  &__link {
    display: block;
    width: 100%;
    height: 100%;

    transition: transform .3s ease-in-out;

    img {
      height: 100%;
      border-radius: 4px;
    }

    @include desktop {
      &:hover {
        transform: scale(1.2);
      }
    }
  }

  @include tablet {
    padding-left: 0;
    padding-right: 0;
  }
}

.callback-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: var(--offset-main) var(--offset-second);
}
