.about {
  position: relative;
  background-color: var(--color-eastbay);


  &__figure {
    position: absolute;
    left: 0;
    top: 0;
  }

  &__text {
    grid-column: 5 span;
    width: 84%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-bottom: 158px;

    @include mobile {
      grid-column: 12 span;
      margin-bottom: var(--offset-second);
    }
  }

  &__title {
    margin-bottom: var(--offset-second);
    color: var(--color-white);
  }

  &__descr {
    margin: 0;
    font: var(--middle-book);
    color: var(--color-white);
  }

  &__quote {
    margin-bottom: var(--offset-second);
    display: inline-block;
    font: inherit;

    &::before,
    &::after {
      display: none;
    }
  }

  &__author {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font: var(--prehigh-regular);
    font-style: normal;

    &::before {
      content: '';
      margin-right: var(--offset-second);
      width: 30px;
      height: 3px;
      background-color: var(--color-white);
    }
  }

  &__image {
    grid-column: 7 span;
    clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);

    img {
      height: 100%;
    }

    @include mobile {
      display: none;
    }
  }
}
