.contacts {
  background-color: #f3f6f6;
 padding:  var(--offset-section) 0;

 &__container {
display: flex;
justify-content: space-between;

@include tablet {
  flex-wrap: wrap;
}
 }

 &__text {
  margin-right: var(--offset-small);
  width: calc(100% - (680px + var(--offset-small)));

  @include tablet {
   margin-right: 0;
   margin-bottom: var(--offset-second);
   width: 100%;
  }
 }

  &__title {
    margin-bottom: var(--offset-section);
  }

  &__item {
    margin-bottom: var(--offset-main);
    font-style: normal;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: var(--offset-main);
    font: var(--small-book);
    text-transform: uppercase;
    color: var(--color-gray);
  }

  &__link {
display: inline-block;
margin-bottom: var(--offset-small);
font: var(--prehigh-book);
font-weight: 700;
color: var(--color-main);

@include tablet {
  font: var(--prehigh-regular);
}
  }

  &__addres {
display: block;

@include tablet {
  font: var( --small-book);
}
  }

  &__map {
    width: 680px;
    height: 500px;

    @include tablet {
    width: 100%;
    }
  }

}
