.cart-modal {
  padding: var(--offset-main);
  width: 580px;

  @include small-tablet {
    width: 550px;
  }

  @include mobile {
    width: 95%;
  }

  &__top {
    margin-bottom: var(--offset-main);
    display: flex;
    align-items: center;
    justify-content: space-between;


  @include mobile {
   flex-direction: column;
   margin-bottom: var(--offset-second);
  }
  }

  &__title {
    margin: 0;
    font: var(--prehigh-regular);
    font-weight: 700;
    color: var(--color-main);


  @include mobile {
    margin-bottom: 10px;
  }
  }

  &__order-number {
    font: var(--small-book);
    font-weight: 400;
    color: var(--color-gray);
  }



  &__input {
    color: var(--color-gray);
    background-color: #f6f6f6;

    &:not(:last-child) {
      margin-bottom: var(--offset-small);
    }
  }

  &__btn {
    margin-top: 30px;

    @include mobile {
     width: 100%;
    }
  }

  @include mobile {
    padding: 15px;
  }
}

.card-modal-order {
  margin-bottom: var(--offset-main);
  padding: 30px;
  border-radius: 4px;
  border: 1px solid #D9D9D9;

  &__top {
    font: var(--middle-book);
    line-height: 14px;
    color: var(--color-gray);

    span {
      font-weight: 700;
      color: var(--color-main);
    }
  }

  &__quantity {
    margin-bottom: var(--offset-second);
  }

  &__sum {
    margin-bottom: var(--offset-second);
  }

  &__show {
    margin-bottom: var(--offset-second);
    display: inline-flex;
    align-items: center;
    color: var(--color-main);

    svg {
      margin-left: 5px;
      width: 7px;
      height: 7px;
      fill: currentColor;
    }
  }

  &__item {

    &:not(:last-child) {
      margin-bottom: 14px;
    }

  }

  @include mobile {
    padding: 10px;
  }
}

.cart-modal-product {
  display: flex;
  align-items: center;

  &__image {
    flex-shrink: 0;
    margin-right: var(--offset-small);
    width: 80px;
    height: 45px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    display: flex;
    flex-grow: 1;
    align-items: center;

    @include mobile {
     flex-direction: column;
    }
  }

  &__text {
    margin-right: auto;
    max-width: 290px;

    @include mobile {
      margin-bottom: 10px;
     }
  }

  &__title {
    margin: 0;
    margin-bottom: var(--offset-small);
    font: var(--small-book);
    line-height: 20px;
  }

  &__price {
    font: var(--middle-book);
    font-weight: 700;
    color: var(--color-main);
  }

  &__delete {
    font: var(--small-book);
    color: var(--color-gray);
  }


}
