.social {
  display: flex;

  &__item {
    &:not(:last-child) {
      margin-right: var(--offset-second);
    }
  }
  &__link {
    display: block;
    width: 44px;
    height: 44px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &--vk {
      background-image: url('../img/VK.svg');
    }

    &--insta {
      background-image: url('../img/instagram.svg');
    }
  }
}
